$ivory: #e4ded3;
$second: #4c4a48;
$clr_gray36: #363534;

$alph9: 0.9;

// Primary colors

.zm-clr-gre4 {
  color: rgba($color: $ivory, $alpha: $alph9);
}

.zm-clr-gr4c {
  color: rgba($color: $second, $alpha: $alph9) !important;
}

.zm-clr-36 {
  color: rgba($color: $clr_gray36, $alpha: $alph9);
}

.zw-clr-fa {
  color: #faf3e7;
}

.zm-clr-pri {
  color: $primary;
}

.zw-clr-pri-opa {
  color: rgba($color: $primary, $alpha: $alph9) !important;
}

// Bg-colors

.bg-white {
  background: $white;
}

.zw-bg-black {
  background: $black;
  position: relative;

  .h2 {
    color: $bg-pri;
  }

  .zw-section-lead__des {
    color: rgba($color: $ivory, $alpha: 0.9) !important;
  }

  &--thrice {
    @extend .zw-section-margin50;

    @include media-breakpoint-down(md) {
      overflow: hidden;
    }

    background: $bg-thrice;
  }
}

.bg-default {
  background: $primary;
}

.bg-pri {
  background: $bg-pri;
}

.bg-blue {
  background: $bg-sec;
}

.bg-black {
  background: $black;
}

.bg-ivory {
  background: $bg-thrice;
}

.text-tick-blue {
  color: $tick-blue;
}

.txt-clr {
  color: $txt-clr;
}

.second09 {
  color: $second09;
}

.txt-drkblue {
  color: #0047ff;
}
