.section-press-release {
    .content {
        position: relative;

        @include media-breakpoint-down(md) {
            margin-bottom: 40px;
        }

        .img {
            img {
                width          : 100%;
                object-fit     : cover;
                object-position: center;

                @include media-breakpoint-down(md) {
                    height: 300px;
                }

                @include media-breakpoint-up(xlg) {
                    height: 550px;
                }

                @include media-breakpoint-up(xxlg) {
                    height: 600px;
                }
            }
        }

        .info {
            background: #202020;
            position  : absolute;
            bottom    : 0;
            padding   : 16px;
            width: 100%;

            @include media-breakpoint-up(md) {
                padding: 24px 28px;
                
            }
        }
    }

    li {
        margin-bottom: 24px;

        &:last-child {
            margin-bottom: 0px;
        }

        img {
            width : 85px;
            height: 85px;

            @include media-breakpoint-up(md) {
                width: 100%;
            }

            @include media-breakpoint-up(lg) {
                height: auto;
            }
        }

        &:last-child {
            .blog-post {
                border-bottom : none;
                padding-bottom: 0;
            }
        }

        .blog-post {
            border-bottom       : 1px solid #E4DED3;
            // padding-top  : 24px;
            padding-bottom      : 1.5rem;
            // &:last-child {
            //     border-bottom: none;
            // }
        }
    }
}

// tabs 
.zw-resources-tabs {
    .nav-pills {
        @include media-breakpoint-down(md) {
            flex-wrap : nowrap;
            overflow-x: scroll;
        }
    }

    .nav-link {
        color         : #4c4a48 !important;
        background    : #e4ded3;
        border        : 1px solid #b3aea9;
        box-sizing    : border-box;
        text-transform: initial;
        margin-right  : 0 !important;
        margin-left   : 0 !important;
        border-radius : unset !important;
        font-weight   : 700;
        font-family   : $Bitter !important;
        font-size     : 21px;
        line-height   : 28px;
        padding       : 19px 42px !important;

        @include media-breakpoint-up(xxlg) {
            padding: 25px 64px !important;
        }

        @include media-breakpoint-down(md) {
            margin-top : 8px;
            font-size  : 14px;
            line-height: 21px;
            padding    : 12px 35px !important;
            white-space: nowrap;
        }

        &::after {
            content         : "";
            background-color: unset !important;
        }
    }

    .nav-link.active {
        color     : #faf3e7 !important;
        background: #192954;
        border    : 1px solid #192954;
        box-sizing: border-box;
    }

    // .tab-content {
    //     margin-top: 24px;
    //     @include media-breakpoint-up(md) {
    //         margin-top: 56px;
    //     }
    // }
}

// cards
.section-press-cards {
    @include media-breakpoint-down(md) {
        padding-top   : 40px !important;
        padding-bottom: 40px !important;
    }

    .card-info-bg-black {
        background    : linear-gradient(0deg, #202020, #202020), url(.png);
        padding       : 32px;
        padding-bottom: 140px;

        @include media-breakpoint-down(md) {
            padding: 16px;
        }
    }

    .card-info-bg-white {
        background : linear-gradient(0deg, #FEFEF4, #FEFEF4), url(.png);
        padding    : 46px;
        padding    : 26px 46px;
        min-height : 160px;
        display    : flex;
        align-items: flex-end;

        @include media-breakpoint-down(md) {
            display: none;
        }
    }
}

// btn view 
.btn-view {
    a {
        color             : #4C4A48;
        font-size         : 21px;
        line-height       : 24px;
        display           : flex;
        justify-content   : end;
        text-decoration   : auto !important;
        margin-bottom     : 48px;
        // font-weight    : 700;
        font-family       : $gowtham-bold !important;

        @include media-breakpoint-down(md) {
            font-size    : 14px;
            line-height  : 21px;
            margin-bottom: 24px;
        }
    }
}

.js-resources-cards {

    .swiper-wrapper {
        @include media-breakpoint-down(md) {
            flex-wrap: nowrap;
        }

        @include media-breakpoint-up(md) {
            width: auto;
        }
    }
}

// media coverage banner
.media-coverage-bnr {
    margin-bottom: 40px;

    @include media-breakpoint-up(md) {
        padding          : 0 13px;
        // padding-bottom: 0;
    }

    .content-info {
        background: #000B36;
        padding   : 68px 68px 68px 68px;

        @include media-breakpoint-down(md) {
            background: #000000;
            padding   : 24px;
        }

        .heading {
            @include media-breakpoint-down(md) {
                margin-bottom: 45px;
            }

            @include media-breakpoint-up(lg) {
                margin-bottom: 140px;
            }

            @include media-breakpoint-up(xxlg) {
                margin-bottom: 180px;
            }
        }
    }

    .gry-card {
        background: #C4C4C4;
    }
}

// latest blog
.section-latest-blog {
    .img {
        width    : 100%;
        // height: 100%;
    }

    .info {
        @include media-breakpoint-down(md) {
            background    : #000000;
            padding       : 16px;
            display       : flex;
            flex-direction: column-reverse;
        }

        p,
        h4 {
            color: $primary;

            @include media-breakpoint-down(md) {
                color: #E4DED3 !important;
            }
        }
    }
}

// case studies
.zw-res-studies-bnr {
    background   : #192954;
    margin-bottom: 40px;
    padding      : 40px 50px;

    @include media-breakpoint-up(md) {
        padding      : 104px;
        margin-bottom: 80px;
        align-items  : center;
    }

    @media (min-width: 1200px) {
        padding    : 154px;
        align-items: flex-start;
    }

    .bnr-heading {
        @include media-breakpoint-up(md) {
            display        : flex;
            justify-content: center;
        }

        h2 {
            @include media-breakpoint-up(md) {
                max-width: 400px;

            }

            @include media-breakpoint-up(lg) {
                margin-right: 4rem;
            }
        }

        img {
            @include media-breakpoint-down(md) {
                width: 100%;
            }
        }
    }
}

