.c-media-dropdown {
    border          : 1px solid #4C4A48;
    background-color: transparent;
    padding         : 12px 20px;

    @media (max-width: 767px) {
        padding: 11px 28px;
    }

    .text {
        color         : #4C4A48 !important;
        font-size     : 21px;
        line-height   : 24px;
        // font-weight: 700;
        font-family   : $gowtham-bold;

        @media (max-width: 767px) {
            font-size: 16px;
        }
    }



    .icon:before {
        content    : '' !important;
        position   : absolute;
        width      : 8px;
        height     : 8px;
        border-top : 2px solid #4C4A48;
        border-left: 2px solid #4C4A48;
        transform  : rotate(225deg);
        top        : 2px;

        @media (max-width: 767px) {
            top: 4px;
        }
    }
}

.m-card-wrp {
    padding: 32px 0 0px;

    @media (max-width: 767px) {
        padding: 20px 0 0px;
    }

    &__img-wrp {
        display: block;
        cursor : pointer;
        height : 154px;

        @media (min-width: 1024px) {
            height: 182px;
        }

        @media (min-width: 1920px) {
            height: 315px;
        }

        position: relative;

        .card-img {
            width     : 100%;
            height    : 100%;
            object-fit: cover;
        }
    }

    &__card {
        text-decoration: none;
        margin-bottom  : 80px;

        @media (max-width: 991px) {
            margin-bottom: 40px;
        }

        @media (max-width: 767px) {
            margin-bottom: 12px;
        }

        &__body {
            @media (max-width: 767px) {
                display: none;
            }

            margin-top: 24px;

            .info {
                font-weight          : 500;
                font-size            : 21px;
                line-height          : 35px;
                font-feature-settings: 'salt'on, 'liga'off;
                color                : #4C4A48;
                opacity              : 0.9;
            }
        }
    }

    .vedio-play {
        position: absolute;
        left    : 24px;
        bottom  : 24px;

        @media (max-width: 767px) {
            left  : 16px;
            bottom: 16px;
        }

        img {
            @media (max-width: 767px) {
                width : 24px;
                height: 24px;
            }
        }
    }
}

.c-model {
    .modal-header {
        border-radius: 0;
        background   : #202020;
        padding      : 16px 32px 16px 16px;
        border       : none;
    }

    .btn-close {
        background-image: url("../img/resources/media-assets/close.svg");
    }

    .modal-dialog {
        width: 100%;

        @media (min-width: 1024px) {
            max-width: 992px
        }

        @media (min-width:1992px) {
            max-width: 1336px;
        }
    }

    .c-img {
        width : 100%;
        height: 206px;

        @media (min-width: 768px) {
            height: 450px;
        }

        @media (min-width: 1992px) {
            height: 797px;
        }

        img {
            object-fit: cover;
            width     : 100%;
            height    : 100%
        }
    }

    .c-date {
        color: #E4DED3;

        @media (max-width: 767px) {
            font-size: 14px;
        }
    }

    .c-desc {
        color      : #E4DED3;
        font-weight: 500;
        font-size  : 21px;
        line-height: 35px;


        @media (max-width: 767px) {
            font-size  : 14px;
            line-height: 21px;
        }
    }

    .c-head {
        font-size  : 24px;
        line-height: 32px;
        color      : #FAF3E7 !important;
        font-weight: 700;

        @media (max-width: 767px) {
            font-size  : 16px;
            line-height: 24px;
        }
    }

    .main-head {
        width          : 100%;
        display        : flex;
        justify-content: space-between;
    }
}

.resource-media-wrap {
    display: none;

    &.active {
        display: block !important;
    }
}

.date-lbl {
    padding    : 12px 20px;
    border     : 2px solid #4C4A48;
    position   : relative;
    width      : auto;
    display    : inline-flex;
    align-items: center;

    @media (max-width: 767px) {
        padding: 8px 12px;
    }

    img {
        cursor     : pointer;
        // position   : absolute;
        // left    : 20px;
    }

    input {
        width       : 210px;
        font-weight : 700;
        padding-left: 40px;
        font-size   : 21px;
        line-height : 24px;
        color       : #4C4A48;
        border      : none;
        background  : transparent;

        &:focus {
            outline: none;
        }

        @media (max-width: 1600px) {
            font-size  : 18px;
            line-height: 24px;
            width      : 185px;
        }

        @media (max-width: 767px) {
            font-size  : 15px;
            line-height: 24px;
            width      : 160px;
        }
    }
}

// iframe video

.iframe-video {
    width : 100%;
    height: 200px;

    @media (min-width: 768px) {
        height: 300px;
    }

    @media (min-width: 1024px) {
        height: 500px;
    }

    @media (max-width: 767px) {
        margin-bottom: 40px;
    }
}

.iframe-video-popup {
    width : 100%;
    height: 100%;
}

// text width 400px in blog details

.txtWid400 {
    @include media-breakpoint-up(md) {
        width: 400px;
    }

    @media (min-width: 1200px) {
        width: 100% !important;
    }
}

.vedio-play {
    position: absolute;
    left    : 24px;
    bottom  : 24px;

    @media (max-width: 767px) {
        left  : 16px;
        bottom: 16px;
    }

    img {
        @media (max-width: 767px) {
            width : 24px;
            height: 24px;
        }
    }
}

.ui.dropdown .menu>.item {
    font-family: $gowtham-bold !important;
}