.zw-logolist {
  display    : flex;
  flex-wrap  : wrap;
  align-items: center;
  height     : 100%;

  &__item {
    width     : 50%;
    text-align: center;

    @include media-breakpoint-up(md) {
      width: 33.33%;
    }

    @include media-breakpoint-up(lg) {
      width: 25%;
    }

    img {
      transform: scale(0.65);

      @include media-breakpoint-down(lg) {
        max-height: 120px;
      }

      @include media-breakpoint-up(md) {
        transform: scale(0.65);
      }

      @include media-breakpoint-up(lg) {
        transform: scale(0.7);
      }

      @include media-breakpoint-up(xxlg) {
        transform: scale(1);
      }
    }
  }

  &--sec {
    .zw-logolist__item {
      width: 33.33%;
    }
  }
}