.zw-hero-panel {
  padding-top: rem(50px);
  padding-bottom: rem(60px);
  color: $bg-pri;

  @include media-breakpoint-up(md) {
    padding-top: rem(100px);
    padding-bottom: rem(100px);
  }

  &__cnt-info {
    width: 360px;
    max-width: 100%;
    @include media-breakpoint-up(xxlg) {
      width: 320px;
    }
  }

  &__figwrap {
    @include media-breakpoint-up(xxlg) {
      margin-left: 3rem;
    }
    img {
      @include media-breakpoint-down(md) {
        width: 150px;
      }
    }
  }
}
