.h1 {
  font-family: $gowtham-black;
  font-size  : rem(32px);
  line-height: rem(35px);

  @include media-breakpoint-up(md) {
    font-size  : rem(40px);
    line-height: rem(55px);
  }

  @include media-breakpoint-up(xl) {
    font-size  : rem(70px);
    line-height: rem(80px);
  }

  @include media-breakpoint-up(xxlg) {
    font-size  : rem(87px);
    line-height: rem(82px);
  }
}

.h2 {
  font-family: $gowtham-black;
  font-size  : rem(24px);
  line-height: rem(30px);

  @include media-breakpoint-up(md) {
    font-size  : rem(30px);
    line-height: rem(40px);
  }

  @include media-breakpoint-up(lg) {
    font-size  : rem(32px);
    line-height: rem(42px);
  }

  @include media-breakpoint-up(xxlg) {
    font-size  : rem(40px);
    line-height: rem(54px);
  }
}

.h3 {
  font-family: $gowtham-black;
  font-size  : rem(22px);
  line-height: rem(32px);

  @include media-breakpoint-up(md) {
    font-size  : rem(21px);
    line-height: rem(31px);
  }

  @include media-breakpoint-up(xxlg) {
    font-size  : rem(24px);
    line-height: rem(34px);
  }
}

.h4 {
  font-size  : rem(20px);
  line-height: rem(30px);

  @include media-breakpoint-up(md) {
    font-size  : rem(28px);
    line-height: rem(38px);
  }

  @include media-breakpoint-up(xxlg) {
    font-size  : rem(32px);
    line-height: rem(42px);
  }
}

// font-weights

.fnt-400 {
  font-weight: $fnt-400;
}

.fnt-500 {
  font-weight: $fnt-500;
}

.fnt-gow-500 {
  font-family: $gowtham-med;
}

.fnt-gow-bold {
  font-family: $gowtham-bold;
}

.fnt-gow {
  font-family: $gowtham-black;
}

.fnt-700,
.fnt-bold {
  font-weight: $fnt-700 !important;
}

.h1Heading,
.pDesc {
  text-align: center;
  margin    : 0 auto;
}

.h1Heading {
  @extend .h1;
  text-transform: uppercase;
  color         : $white;
}

.pDesc {
  @extend .fnt-24;
  color: $white;
}

.brd-btn {
  border-bottom: 1px solid $white;
}

.fnt-bitter {
  font-family: $Bitter !important;
}