.zw-metrics-blocklist {
  display      : flex;
  margin-bottom: 0;

  @include media-breakpoint-down(md) {
    flex-wrap: wrap;
  }

  &__item {
    width : 50%;
    cursor: pointer;

    @include media-breakpoint-down(md) {
      margin-bottom: 1rem;
    }

    @include media-breakpoint-up(md) {
      border-right: 1px solid $black;
      width       : 33.33%;
    }

    padding : {
      left  : 1rem !important;
      top   : 1rem !important;
      bottom: 1rem !important;
      right : 1rem !important;
    }

    @include media-breakpoint-up(md) {
      padding : {
        left  : 2rem !important;
        top   : 1.5rem !important;
        bottom: 1.4rem !important;
        right : 0 !important;
      }
    }

    transition: $transition;

    &:last-child {
      border-right: 0;
    }

    &:hover {
      background: #030303;
      transition: $transition;
      border    : none;

      .zw-blocklist-number {
        color: #faf3e7 !important;
      }

      .zm-clr-gr4c {
        color: rgba($color: $ivory, $alpha: $alph9) !important;
      }
    }
  }
}

.zw-blocklist-number {
  display: block;
}

.zw-tabs-sections {
  overflow: hidden;
}

// Info

.zw-media {
  align-items: flex-start;
  position   : relative;

  &__left {
    padding-right: rem(20px);
    margin-right : rem(20px);
    position     : relative;

    @include media-breakpoint-up(lg) {
      padding-right: rem(50px);
      margin-right : rem(50px);
    }

    @include media-breakpoint-down(md) {
      position: absolute;
      left    : 0;
      top     : 1rem;
    }

    &::after {
      content   : "";
      position  : absolute;
      top       : 0.5rem;
      right     : 0;
      background: $second;
      width     : 1px;
      height    : calc(100% - 10%);

      @include media-breakpoint-up(md) {
        height: calc(100% - 20%);
      }
    }
  }

  &__right {
    display: flex;

    @include media-breakpoint-down(md) {
      flex-direction: column;
      padding-top   : 1rem;
    }

    .h2 {
      @include media-breakpoint-down(md) {
        font-size    : rem(16px) !important;
        line-height  : rem(24px) !important;
        padding-left : 6.5rem;
        margin-bottom: 1.2rem;
      }

      @include media-breakpoint-between(md, lg) {
        font-size  : rem(24px) !important;
        line-height: rem(34px) !important;
      }
    }

    @include media-breakpoint-up(lg) {
      padding-right: 3rem;
    }

    @include media-breakpoint-up(xl) {
      padding-right: 5rem;
    }
  }
}

.zw-number-lg {
  font-family: $gowtham-med;
  font-size  : rem(50px);
  line-height: rem(55px);

  @include media-breakpoint-down(md) {
    padding-top: 0.5rem;
    display    : inline-block;
  }

  @include media-breakpoint-up(lg) {
    font-size  : rem(100px);
    line-height: rem(114px);
  }

  @include media-breakpoint-up(xxlg) {
    font-size  : rem(120px);
    line-height: rem(164px);
  }
}

.zw-max220 {
  @include media-breakpoint-down(lg) {
    max-width: 220px;
  }
}

// Quote sliders

.zw-quote-slider-wrapper {
  color  : rgba($color: $ivory, $alpha: $alph9);
  padding: 2rem 1rem 2rem 1rem;

  @include media-breakpoint-up(md) {
    padding: 3rem 2rem 3rem 2rem;
  }

  @include media-breakpoint-up(xl) {
    padding: 5rem 6rem 5rem 6rem;
  }
}

.zw-quote-slider-wrapper {
  height     : 100%;
  display    : flex;
  align-items: center;

  .swiper-wrapper {
    display    : flex;
    align-items: center;
  }
}

.zw-quote-slider-text {
  font-family: $gowtham-bold;
  font-weight: $fnt-700;
  display    : inline-block;
}

.zw-quote-slider-author {
  @extend .fnt-21;
  font-weight: $fnt-700;
  color      : $white;
}

.zw-image-slider-wrapper {
  @include media-breakpoint-up(md) {
    height: 100%;

    .swiper-container,
    img {
      height: 100%;
    }
  }
}

.imgxh636 {
  @include media-breakpoint-down(md) {
    height: 200px;
    width : 100%;
  }
}


.img-fit-gif {
  @include media-breakpoint-up(lg) {
    max-width : 100% !important;
    object-fit: fill !important;
  }
}