.zw-border-info {
  border-bottom: 0 !important;
  .zwPanel {
    @include media-breakpoint-down(md) {
      padding-top: 1.8rem !important;
      padding-left: 2rem !important;
    }
    &:hover {
      .zw-block-lists__item {
        color: rgba($color: #e4ded3, $alpha: 0.9) !important;
        &::before {
          background: rgba($color: #e4ded3, $alpha: 0.9) !important;
        }
      }
    }
    @include media-breakpoint-up(md) {
      border-bottom: 1px solid #e6dfd4 !important;
    }
  }
  h3 {
    padding-right: 1.5rem !important;
  }
}

.zw-block-lists {
  position: relative;
  color: rgba($color: $second, $alpha: 0.9);
  margin-top: 0.5rem;
  padding-left: 1.5rem !important;
  &__item {
    position: relative;
    @include media-breakpoint-down(md) {
      padding-bottom: 0.8rem !important;
      font-size: 14px !important;
      line-height: 24px !important;
    }
    &::before {
      content: "";
      position: absolute;
      top: 8px;
      left: -0.8rem;
      height: 5px;
      width: 5px;
      border-radius: 50%;
      //   background: rgba(228, 222, 211, 0.9);
      background: $second;
    }
  }
}
