.zWContactForm {
  .ui.selection {
    &.dropdown {
      color        : $bg-thrice;
      background   : $primary;
      border-radius: 0;
      width        : 100%;
      min-width    : 100%;
      max-width    : 100%;

      input {
        color: $bg-thrice;
      }
    }

    .menu {
      .item {
        color: $bg-thrice;
      }

      background: $primary;

      >.item:hover {
        background: $white;
        color     : $black;
      }

      >.item {
        border-top: none;
      }
    }
  }

  .ui.form {
    .field {
      clear : both;
      margin: 0 0 24px;

      input {
        background   : $transparent;
        border-radius: 0 !important;
        border       : none;
        border-bottom: 1px solid $white;
        padding      : 5px 30px 5px 0;
        margin-bottom: 10px;
        color        : $bg-thrice;
      }

      &>label {
        font-weight: $fnt-400;
        @extend .fnt-21;
        color: $bg-pri;
      }

      &.error input {
        background  : $primary;
        border-color: $dang;
        color       : $dang;
      }

      .prompt.label {
        white-space: normal;
        background : transparent !important;
        border     : none !important;
        color      : $dang !important;
      }
    }
  }
}

.zw-drpdefult-txt {
  color      : $bg-pri !important;
  font-weight: $fnt-500;
}

.menu--dropdown {
  width        : 100% !important;
  padding      : 0.5rem 1.2rem 1rem 1.2rem !important;
  background   : $bg-pri !important;
  border-radius: 0 !important;
  box-shadow   : none !important;
  top          : 115%;

  @include media-breakpoint-down(lg) {
    position: relative !important;
    top     : 0 !important;
    border  : none !important;
  }

  @include media-breakpoint-up(lg) {
    box-shadow: 0px 0px 6px rgba(87, 87, 87, 0.16);
  }
}

li>a.item__link {
  text-decoration: none;
  margin-top     : 0.8rem !important;
  display        : inline-block;
  color          : $second;
  transition     : $transition;

  &:hover,
  &:focus {
    color     : $primary;
    transition: $transition;
  }
}

// resource dropdown

.menu--resDropdown {
  width        : 100% !important;
  // padding: 0.5rem 1.2rem 1rem 1.2rem !important;
  background   : $bg-pri !important;
  border-radius: 0 !important;
  box-shadow   : none !important;
  top          : 115%;

  @include media-breakpoint-down(lg) {
    position: relative !important;
    top     : 0 !important;
    border  : none !important;
    padding : 1rem;
  }

  @include media-breakpoint-up(lg) {
    box-shadow: 0px 0px 6px rgba(87, 87, 87, 0.16);
  }
}