ul,
li {
  padding: 0 !important;
  // margin    : 0 !important;
  list-style: none;
}

video::-webkit-media-controls-panel {
  padding: 0 30px;
  background-image: linear-gradient(
    transparent,
    transparent
  ) !important; //Transparent for your case
}

.ui.fluid.dropdown > .dropdown.icon {
  width: 100%;
  display: flex;
  text-align: end;
  justify-content: flex-end;
}

#NetwerkBoxlink1 {
  transition: $transition;
  &:hover {
    path {
      transition: $transition;
      fill: red;
    }
  }
}

.filter-two {
  transition: $transition;
  &:hover {
    path {
      transition: $transition;
      fill: green;
    }
  }
}
