.zw-text-overlay {
  @include media-breakpoint-up(lg) {
    background: rgba($color: #020408, $alpha: 0.6);
  }
}

.zw-banner-media {
  @include media-breakpoint-up(md) {
    display: flex;
    margin: 0 auto;
    max-width: 750px;
  }

  @include media-breakpoint-up(lg) {
    max-width: 850px;
  }

  @include media-breakpoint-up(xl) {
    max-width: 1000px;
  }

  @include media-breakpoint-up(xxlg) {
    max-width: 1200px;
  }

  padding: {
    top: 2rem;
  }

  @include media-breakpoint-up(lg) {
    padding: {
      top: 2.4rem;
      bottom: 2.4rem;
    }
  }

  &__heading {
    font-family: $gowtham-black;

    @include media-breakpoint-down(lg) {
      text-align: center;
    }

    @include media-breakpoint-up(lg) {
      font-size: rem(32px);
      line-height: rem(42px);
      margin-right: 3.5rem;
    }

    @include media-breakpoint-up(xxlg) {
      font-size: rem(45px);
      line-height: rem(55px);
    }
  }

  @include media-breakpoint-down(md) {
    p {
      background: rgba($color: #020408, $alpha: 0.6);
      padding: 1.5rem;
      color: rgba($color: #e4ded3, $alpha: 0.9);
    }
  }
}

.zw-max800 {
  width: 800px;
  margin: 0 auto;
  max-width: 100%;
}

// List info

.zw-main-list {
  position: relative;

  &__content {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    z-index: 10;
  }

  img {
    height: 100%;
  }
}

.zw-ma-blocklist {
  height: 100%;
  display: flex;

  @include media-breakpoint-up(md) {
    // padding: 2rem 1rem 2rem 1rem !important;
  }

  @include media-breakpoint-up(lg) {
    padding: 2rem 1rem 4rem 1rem !important;
  }

  @include media-breakpoint-up(xxl) {
    padding: 2rem 2rem 5rem 2rem !important;
  }

  @include media-breakpoint-up(xxlg) {
    padding: 2rem 5rem 5rem 2rem !important;
  }

  &__item {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    margin-bottom: 1rem;
    width: 50%;
    padding-left: 2rem !important;

    @include media-breakpoint-up(xl) {
      padding-left: 3rem !important;
    }

    @include media-breakpoint-up(xxlg) {
      padding-left: 4rem !important;
    }
  }

  &__text {
    display: inline-block;

    @include media-breakpoint-up(xl) {
      padding-right: 1rem;
    }
  }
}

.zw-ma-blocklist-link {
  @extend .zm-clr-gre4;
  font-weight: $fnt-700;
  font-size: rem(18px);
  line-height: rem(28px);
  width: 100%;

  @include media-breakpoint-up(md) {
    margin-bottom: 0.5rem;
  }

  @include media-breakpoint-up(xl) {
    margin-bottom: 1rem;
  }

  @include media-breakpoint-up(xxl) {
    margin-bottom: 1.5rem;
  }

  &:last-child {
    margin-bottom: 0;
  }

  @include media-breakpoint-up(lg) {
    font-size: rem(20px) !important;
    line-height: rem(30px) !important;
  }

  @include media-breakpoint-up(xxlg) {
    font-size: rem(24px) !important;
    line-height: rem(34px) !important;
  }

  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: space-between;

  padding: {
    top: 0.8rem;
    bottom: 0.8rem;
    left: 1rem;
    right: 1rem;
  }

  @include media-breakpoint-up(xl) {
    padding: {
      top: 1.2rem;
      bottom: 1.2rem;
      left: 1rem;
      right: 1rem;
    }
  }

  @include media-breakpoint-up(xxl) {
    padding: {
      right: 2rem;
    }
  }

  @include media-breakpoint-up(xxlg) {
    padding: {
      top: 1.2rem;
      bottom: 1.2rem;
      left: 1rem;
      right: 3rem;
    }
  }

  transform: $transition;
  border-top: 1px solid transparent;
  border-bottom: 1px solid transparent;

  .icon {
    opacity: 0;
  }

  &:hover,
  &:focus {
    border-color: #faf3e7;
    border-color: #faf3e7;
    background: rgba($color: #e4ded3, $alpha: 0.2);
    @extend .zm-clr-gre4;
    transform: $transition;

    .icon {
      opacity: 1;
    }
  }
}

// mobile slider

$alph9: 0.9;

.mobile-manufacturing,
.js-mobile-TabSlider-manufacturing-industries {
  li {
    @include media-breakpoint-down(md) {
      padding: 8px 10px !important;
      padding-left: 0 !important;
      margin-right: 12px;
    }
  }

  h2 {
    @include media-breakpoint-down(md) {
      flex: none !important;
      margin-bottom: 0;
      font-family: $gowtham-bold;
      color: rgba($color: $second, $alpha: $alph9);
    }
  }

  .swiper-slide {
    border-left: 1px solid rgba($color: #192954, $alpha: $alph9);
    // color       : rgba($color: #192954, $alpha: $alph9);
    padding-left: 0.8rem !important;
    border-right: unset !important;

    &:first-child {
      border-left: none;
    }
  }

  .zw-malist {
    &__link {
      text-decoration: none;
      display: flex;
      align-items: flex-end;
      justify-content: space-between;
    }

    // &__item {
    //   border-bottom: 1px solid #4c4a48;
    // }
  }
}

// Only mobile slider

.mobile-manufacturing {
  .accordion-flush .accordion-item,
  .accordion-button {
    background: transparent;
  }
  .accordion-button {
    padding-right: 0;
    padding-left: 0;
    font-size: 18px !important;
    line-height: 28px !important;
    color: rgba($color: $second, $alpha: $alph9);
  }
}
