.zw-box-innerspace {
  padding: {
    left: 1.5rem;
    right: 1.5rem;
  }
  @include media-breakpoint-up(md) {
    padding: {
      left: 3rem;
      right: 3rem;
    }
  }
  @include media-breakpoint-up(xl) {
    padding: {
      left: 5rem;
      right: 5rem;
    }
  }
  @include media-breakpoint-up(md) {
    h2 {
      word-break: break-word;
    }
  }
}

.zw-mob-bg-black {
  @include media-breakpoint-down(md) {
    background: $bg-thrice;
    padding-bottom: 2.5rem;
    .zw-bg-black {
      padding: {
        left: 0rem;
        right: 0rem;
        bottom: 1.5rem;
      }
      background: $bg-thrice;
    }
    h2 {
      color: $primary;
    }
    .zw-section-lead__des {
      color: rgba($color: $second, $alpha: $alph9) !important;
    }
  }
}

.zw-leader {
  text-decoration: none;
  display: block;
}

.zw-img-filter {
  filter: grayscale(100%);
}
