.zw-explore-solution-lg {
  .nav-pills {
    @include media-breakpoint-down(md) {
      flex-wrap: nowrap;
      overflow-x: scroll;
    }
  }
  .nav-link {
    color: #4c4a48 !important;
    background: #e4ded3;
    border: 1px solid #b3aea9;
    box-sizing: border-box;
    text-transform: initial;
    margin-right: 0 !important;
    margin-left: 0 !important;
    border-radius: unset !important;
    font-weight: 700;
    font-family: $Bitter !important;
    font-size: 21px;
    line-height: 28px;
    padding: 24px 16px !important;
    @include media-breakpoint-up(xxlg) {
      padding: 24px 52px !important;
    }
    @include media-breakpoint-down(md) {
      margin-top: 8px;
      font-size: 14px;
      line-height: 21px;
      padding: 12px 35px !important;
      white-space: nowrap;
    }
    &::after {
      content: "";
      background-color: unset !important;
    }
  }
  .nav-link.active {
    color: #faf3e7 !important;
    background: #192954;
    border: 1px solid #192954;
    box-sizing: border-box;
  }
}

// black
// Block panels
.zw-block-panel-container {
  background-color: #202020;
  padding: {
    top: rem(30px);
    bottom: rem(40px);
    left: rem(32px);
  }
  @include media-breakpoint-up(md) {
    padding: {
      top: rem(60px);
      bottom: rem(60px);
    }
  }
  @include media-breakpoint-up(lg) {
    padding: {
      top: rem(100px);
      bottom: rem(100px);
      left: rem(128px);
      right: rem(128px);
    }
  }
  @include media-breakpoint-up(xxlg) {
    padding: {
      top: rem(100px);
      bottom: rem(100px);
      left: rem(160px);
      right: rem(160px);
    }
  }
  &--sec {
    padding-bottom: rem(40px);
    @include media-breakpoint-up(md) {
      padding-bottom: rem(80px);
    }
    @include media-breakpoint-up(lg) {
      padding-bottom: rem(180px);
    }
  }
  .ZW-unOrderList {
    &__desc {
      color: #e4ded3;
      opacity: 0.9;
      position: relative;
      &::before {
        content: "";
        position: absolute;
        // top          : 50%;
        transform: translateY(-50%);
        left: -0.8rem;
        height: 5px;
        width: 5px;
        border-radius: 50%;
        background: $txt-clr;
      }
    }
  }
}

.ZW-unOrderList--listnew {
  // column-count: 2;
  @include media-breakpoint-up(lg) {
    column-count: 4;
  }
  li {
    width: 90%;
    @extend .mb-2;
    @extend .mb-xl-5;
    &:last-child {
      margin-bottom: 0 !important;
      p {
        margin-bottom: 0 !important;
      }
    }
    @include media-breakpoint-up(md) {
      // width: 33%;
    }
    @include media-breakpoint-up(xl) {
      // width: 24%;
    }
  }
  p {
    @extend .second09;
    &::before {
      background: $second09;
    }
  }
}
