.zw-section-lead {
  padding-right: 2rem;

  @include media-breakpoint-up(md) {
    padding-right: 4rem;
  }

  @include media-breakpoint-up(xl) {
    padding-right: 7rem;
  }
}

.section-padding150 {
  padding : {
    top   : rem(40px);
    bottom: rem(40px);
  }

  @include media-breakpoint-up(md) {
    padding : {
      top   : rem(80px);
      bottom: rem(80px);
    }
  }

  @include media-breakpoint-up(xl) {
    padding : {
      top   : rem(120px);
      bottom: rem(120px);
    }
  }
}

.section-padding80 {
  padding : {
    top   : rem(30px);
    bottom: rem(30px);
  }

  @include media-breakpoint-up(md) {
    padding : {
      top   : rem(60px);
      bottom: rem(60px);
    }
  }

  @include media-breakpoint-up(lg) {
    padding : {
      top   : rem(80px);
      bottom: rem(80px);
    }
  }
}

.zw-section-padding65 {
  padding : {
    top   : rem(30px);
    bottom: rem(30px);
  }

  @include media-breakpoint-up(md) {
    padding : {
      top   : rem(50px);
      bottom: rem(50px);
    }
  }

  @include media-breakpoint-up(lg) {
    padding : {
      top   : rem(65px);
      bottom: rem(65px);
    }
  }
}

.zw-section-margin50 {
  margin : {
    top   : rem(30px);
    bottom: rem(30px);
  }

  @include media-breakpoint-up(lg) {
    margin : {
      top   : rem(60px);
      bottom: rem(60px);
    }
  }
}

.zw-section-padding80 {
  padding: rem(30px) rem(15px);

  @include media-breakpoint-up(md) {
    padding: rem(50px);
  }

  @include media-breakpoint-up(lg) {
    padding: rem(80px);
  }
}

.zw-section-padding60 {
  @include media-breakpoint-up(md) {
    padding: rem(60px) rem(0px);
  }
}

.zw-section-paddingb90 {
  padding: {
    top   : rem(0px);
    bottom: rem(40px);
  }

  @include media-breakpoint-up(md) {
    padding: {
      top   : rem(0px);
      bottom: rem(90px);
    }
  }
}

.zw-section-paddingb60 {
  padding: {
    top   : rem(0px);
    bottom: rem(40px);
  }

  @include media-breakpoint-up(md) {
    padding: {
      top   : rem(0px);
      bottom: rem(64px);
    }
  }
}