body {
  font-family: $Bitter;
  font-weight: $fnt-500;
  overflow-x: hidden;
  color: $primary;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  background-color: $bg-pri;

  p {
    font-size: rem(14px);
    line-height: rem(22px);
    margin: 0;
    padding: 0;

    @include media-breakpoint-up(lg) {
      font-size: rem(16px);
      line-height: rem(26px);
    }

    @include media-breakpoint-up(xxlg) {
      font-size: rem(21px);
      line-height: rem(32px);
    }
  }
}

.container {
  @include media-breakpoint-up(xxlg) {
    max-width: 1520px;
  }
}

.zw-px-mob {
  @include media-breakpoint-down(md) {
    padding: 0 !important;
  }
}

.zw-pe-mob {
  @include media-breakpoint-down(md) {
    padding-right: 0;
  }
}

.zw-only-desktop {
  @include media-breakpoint-down(md) {
    display: none !important;
  }
}

.zw-only-mobile {
  @include media-breakpoint-up(lg) {
    display: none !important;
  }
}

.zw-only-mobile-md {
  @include media-breakpoint-up(md) {
    display: none !important;
  }
}

.zw-only-mobile-hidden {
  @include media-breakpoint-down(md) {
    overflow: hidden;
  }
}

.zw-only-tablet-hidden {
  @include media-breakpoint-down(lg) {
    overflow: hidden;
  }
}

.zw-only-desktop-hidden {
  @media (max-width: 1280px) {
    overflow: hidden;
  }
}

// paddings

.zw-topspace8 {
  padding-top: 6rem;

  @include media-breakpoint-up(md) {
    padding-top: 8rem;
  }
}

.zw-topspace7 {
  padding-top: 5rem;

  @include media-breakpoint-up(md) {
    padding-top: 6.5rem;
  }
}

// images scaleout

.zw-scaleout-09 {
  @include media-breakpoint-up(lg) {
    transform: scale(0.9);
  }
}

// flex
.zw-hv-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.flex-colum-mb {
  @include media-breakpoint-down(md) {
    flex-direction: column-reverse;
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fade-out {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

.h450 {
  @media (min-width: 1356px) {
    height: 450px;
  }
}

.borderBottom {
  border-bottom: 1px solid $bg-thrice;
}

.borderSecondBottom {
  border-bottom: 1px solid $second02;
  @include media-breakpoint-up(md) {
    border-bottom: 1px solid $second;
  }
}

.borderGray {
  border: 1px solid $tin-gray;
}

.borderPrimaryTopBottom {
  @include media-breakpoint-up(md) {
    border-top: 1px solid $primary;
    border-bottom: 1px solid $primary;
  }
}
.wrdBreak {
  word-break: break-all;
}

//font weights

.zw-mob-bold {
  @include media-breakpoint-down(md) {
    font-weight: 600;
  }
}
