.zw-hero-card {
  position: relative;
  width   : 100%;
  cursor  : pointer;

  // body
  &__body {
    position  : absolute;
    left      : 0;
    right     : 0;
    width     : 100%;
    height    : 100%;
    top       : 0;
    bottom    : 0;
    color     : $txt-clr;
    transition: $transition;

    @include media-breakpoint-up(lg) {
      background: #2e2e2e;
    }
  }

  // Content info
  &__cnt-wrap {
    padding-top  : rem(50px);
    padding-left : rem(30px);
    padding-right: rem(20px);
    height       : 100%;

    @include media-breakpoint-up(md) {
      padding-top  : rem(80px);
      padding-left : rem(40px);
      padding-right: rem(20px);
    }

    @include media-breakpoint-up(xl) {
      padding-top  : rem(100px);
      padding-left : rem(80px);
      padding-right: rem(40px);
    }
  }

  &__des {
    font-weight   : $fnt-500;
    padding-bottom: rem(16px);
  }

  .zw-hero-card__body {
    @include media-breakpoint-down(lg) {

      .zw-txt-number,
      .zm-sub-text {
        color: $txt-clr !important;
      }
    }
  }

  // Hover content
  &:hover .zw-hero-card__body {
    @include media-breakpoint-up(lg) {
      color     : $txt-clr !important;
      background: transparent !important;
      transition: $transition;

      .zw-txt-number,
      .zm-sub-text {
        color: $txt-clr !important;
      }
    }
  }
}

.zw-h700-fix {
  height    : rem(400px);
  max-height: 100%;

  @include media-breakpoint-up(md) {
    height: rem(450px);
  }

  @include media-breakpoint-up(xl) {
    height: rem(500px);
  }

  img {
    height: 100%;
    width : 100%;
  }

  &--small {
    img {
      width: 100%;
    }

    height: rem(400px);

    @include media-breakpoint-up(md) {
      height: rem(500px);
    }

    @include media-breakpoint-up(xl) {
      height: rem(540px);
    }
  }

  .img-md-icon {
    width : 50px;
    height: 50px;
  }
}

.zw-cbg-pri {
  @include media-breakpoint-up(lg) {
    background: $bg-pri !important;
    color     : $second !important;
  }

  &:hover .zw-btn {
    border    : 1px solid $bg-pri;
    background: $bg-pri;
    color     : rgba($color: $primary, $alpha: 0.9);
  }

  .zw-txt-number {
    color: $primary;
  }

  .zm-sub-text {
    color: rgba($color: #4c4a48, $alpha: $alph9);
  }
}

.zw-cbg-info {
  @include media-breakpoint-up(lg) {
    background: $blue !important;
  }
}

.zw-cbg-primary {
  @include media-breakpoint-up(lg) {
    background: $primary !important;
    color     : $white !important;
  }
}

.zw-txt-number {
  font-size  : rem(32px);
  line-height: rem(36px);
  font-family: $gowtham-bold;
  display    : block;
  transition : $transition;
  color      : $bg-pri;

  @include media-breakpoint-up(md) {
    font-size  : rem(46px);
    line-height: rem(50px);
  }

  @include media-breakpoint-up(lg) {
    font-size  : rem(50px);
    line-height: rem(50px);
  }

  @include media-breakpoint-up(xl) {
    font-size  : rem(56px);
    line-height: rem(56px);
  }
}

.zm-sub-text {
  @extend .fnt-21;
  font-weight: $fnt-700;
  transition : $transition;
}

// Our number list
.zw-number-list {
  // padding-top: 0.5rem !important;

  &__item {
    position         : relative;
    // margin-top: 1rem !important;
    // padding-bottom: 1rem !important;

    &:first-child {
      margin-top: 0 !important;
    }

    &::after {
      content   : "";
      position  : absolute;
      top       : 50%;
      right     : 0;
      height    : rem(70px);
      width     : 1px;
      background: #e4ded3;
      transform : translateY(-50%);
    }

    &:last-child::after {
      display: none;
    }
  }
}

.zwPanel {
  background: $bg-pri;
  padding   : rem(18px) !important;
  position  : relative;
  transition: $transition;
  height    : auto;
  cursor    : pointer;

  @include media-breakpoint-up(md) {
    padding: rem(30px) rem(20px) !important;
  }

  &:hover {
    color     : $white;
    background: $black;
    transition: $transition;

    .zm-clr-gr4c {
      color: $txt-clr !important;
    }

    svg polygon,
    svg polyline,
    svg circle,
    svg path,
    svg line {
      stroke: $white;
    }
  }
}

.mt-150 {
  margin-top: -70px;

  @include media-breakpoint-up(md) {
    margin-top: -150px;
  }
}

.top-40 {
  top: 35%;

  @include media-breakpoint-up(md) {
    top: 40%;
  }
}

// Mobile number slider

.js-mb-ntwrk-slider {
  li {
    border : 1px solid #bebebe;
    padding: 1.2rem 1.5rem !important;
  }
}