.zw-modal-dialog-gif {
  .modal-content {
    background: transparent;
    border: none;
  }
  .moday-head {
    position: absolute;
    top: -0.8rem;
    right: -0.2rem;
    padding: 0 !important;
    border-bottom: 0;
    z-index: 100;
    @include media-breakpoint-up(md) {
      top: -1.5rem;
    }
  }

  .modal-body {
    padding: 0;
  }

  .btn-close {
    opacity: 1;
    &:focus {
      outline: none;
      box-shadow: none;
    }
  }
}
