.zw-hero-panelbox {
  @include media-breakpoint-down(lg) {
    flex-direction: column-reverse;
  }

  &__content {
    position: relative;

    @include media-breakpoint-down(lg) {
      padding: 1.6rem;
    }

    @include media-breakpoint-between(lg, xl) {
      padding: {
        top   : 3rem;
        left  : 3rem;
        right : 3.5rem;
        bottom: 2.5rem;
      }
    }

    @include media-breakpoint-up(xl) {
      padding-top: 4rem;
      max-width  : 530px;
    }

    @include media-breakpoint-up(xxlg) {
      max-width: 600px;
    }
  }

  &__heading {
    @include media-breakpoint-down(lg) {
      font-size  : rem(32px) !important;
      line-height: rem(42px) !important;
      position   : absolute;
      top        : -50%;
      left       : 50%;
      transform  : translate(-50%, -50%);
      z-index    : 10;
      width      : 300px;
      max-width  : 100%;
      text-align : center;
      margin     : 0 auto;
    }

    @include media-breakpoint-between(md, lg) {
      top: -65%;
    }
  }

  &__fig {
    @include media-breakpoint-down(md) {
      height: 205px;
      width : 100%;
    }

    @include media-breakpoint-between(md, lg) {
      height: 300px;
      width : 100%;
    }

    @include media-breakpoint-between(lg, xl) {
      height: 100%;
    }

    @include media-breakpoint-up(xl) {
      max-height: 100%;
      height    : 520px;
    }

    @include media-breakpoint-up(xxlg) {
      height: 618px;
    }
  }
}

.zw-panelbox-twolines {
  top: -30%;
}

.zw-panelbox-htwolines {
  top: -60%;
}

