.zw-backpage {
  display      : flex;
  align-items  : center;
  margin-bottom: rem(20px);

  @include media-breakpoint-down(lg) {
    text-decoration: none;

    p {
      color: rgba($color: $second, $alpha: 0.6) !important;
    }
  }

  @include media-breakpoint-up(lg) {
    margin-bottom: rem(40px);
  }

  @include media-breakpoint-up(xl) {
    margin-bottom: rem(60px);
  }

  &__icon {
    transform: rotate(180deg);
    color    : $second;

    @include media-breakpoint-up(lg) {
      color: #faf3e7;
    }
  }

  &__icon-black {
    transform: rotate(180deg);
    color    : #4C4A48;

    @include media-breakpoint-up(lg) {
      color: #4C4A48;
    }
  }

  &__text {
    color: rgba($color: #e4ded3, $alpha: 0.6) !important;
  }

  &__text-black {
    color: #4C4A48;
  }

  &__white06 {
    transform: rotate(180deg);
    color    : $second06 !important;

    @include media-breakpoint-up(md) {
      // color: $txt-clr06 !important;
      color: $bgThrice06 !important;
    }
  }

  &__black06 {
    transform: rotate(180deg);
  }
}

// methods

.zw-hero-panel-casting-methods {
  margin-bottom: 40px;

  @include media-breakpoint-up(md) {
    margin-bottom: 64px;
  }

  @include media-breakpoint-down(md) {
    padding-top: 40px;
  }

  .zw-hero-panel-casting-methods-card {
    background: #e4ded3;
    padding   : 24px;

    @include media-breakpoint-up(md) {
      padding: 70px;
      display: flex;
    }

    @include media-breakpoint-up(lg) {
      padding: 90px;
    }

    // @include media-breakpoint-up(xl) {
    //   padding: 90px 120px;
    // }
  }

  figure {
    img {
      @include media-breakpoint-up(md) {
        height: 310px;
      }
    }
  }
}

