// explore solution

.zw-explore-solution {
  // margin-bottom: 40px;
  overflow     : hidden;
  padding-left : 0;
  padding-right: 0;

  // @include media-breakpoint-up(md) {
  //     margin-bottom: 90px;
  // }

  .swiper-container {
    @include media-breakpoint-up(md) {
      overflow: visible;
    }
  }

  .swiper-wrapper {
    margin-bottom: 0;
  }

  .nav-pills {
    @include media-breakpoint-down(md) {
      flex-wrap : nowrap;
      overflow-x: scroll;
    }
  }

  .nav-link {
    color         : #4c4a48 !important;
    background    : #e4ded3;
    border        : 1px solid #b3aea9;
    box-sizing    : border-box;
    text-transform: initial;
    margin-right  : 0 !important;
    margin-left   : 0 !important;
    border-radius : unset !important;
    font-weight   : 700;
    font-family   : $Bitter !important;
    font-size     : 21px;
    line-height   : 28px;
    padding       : 24px 58px !important;

    @include media-breakpoint-up(xxlg) {
      padding: 24px 86px !important;
    }

    @include media-breakpoint-down(md) {
      margin-top : 8px;
      font-size  : 14px;
      line-height: 21px;
      padding    : 12px 35px !important;
      white-space: nowrap;
    }

    &::after {
      content         : "";
      background-color: unset !important;
    }
  }

  .nav-link.active {
    color     : #faf3e7 !important;
    background: #192954;
    border    : 1px solid #192954;
    box-sizing: border-box;
  }

  // .tab-content {
  //     margin-top: 24px;

  //     @include media-breakpoint-up(md) {
  //         margin-top: 56px;
  //     }
  // }

  .swiper-slide {
    @include media-breakpoint-up(md) {
      // padding-right: 1rem !important;
    }
  }

  .card-heading {
    position: absolute;
    bottom  : 20px;
    left    : 20px;

    @include media-breakpoint-up(md) {
      bottom: 40px;
      left  : 40px;
    }
  }

  figure {
    margin: 0;

    img {
      height: 180px;

      @include media-breakpoint-up(md) {
        height: 100%;
      }
    }
  }
}

// finishing touch

.zw-die-casting-finishing-touch {
  padding   : 32px 0 20px 0;
  background: #e4ded3;
  overflow  : hidden;

  @include media-breakpoint-up(md) {
    padding: 64px 0 80px 0;
  }

  .swiper-container {
    @include media-breakpoint-up(md) {
      overflow: visible;
    }
  }

  .swiper-wrapper {
    @include media-breakpoint-down(md) {
      margin-bottom: 0;
      padding-right: 0;
    }
  }

  .js-die-casting-finishing-touch {
    // margin-top: 21px;

    @include media-breakpoint-up(md) {
      // margin-top: 64px;
    }

    .card-heading {
      position : absolute;
      top      : 50%;
      transform: translateY(-50%);
      left     : 24px;

      @include media-breakpoint-up(md) {
        left     : 40px;
        max-width: 250px;
      }

      @include media-breakpoint-up(lg) {
        left     : 60px;
        max-width: 380px;
      }

      @include media-breakpoint-up(xl) {
        left: 80px;
      }

      h2 {
        color           : #faf3e7 !important;
        // padding-right: 5rem;
      }

      p {
        color           : #faf3e7 !important;
        // padding-right: 5rem;
      }
    }
  }

  figure {
    img {
      width : 100%;
      height: 400px;

      @include media-breakpoint-up(md) {
        height: 420px;
      }

      @include media-breakpoint-up(lg) {
        height: 500px;
      }

      @include media-breakpoint-up(xl) {
        height: 600px;
      }
    }
  }
}

// hide scrollbar

.nav::-webkit-scrollbar {
  display: none;
}


