.zm-banner-slider {
  position: relative;
  padding-top: 3rem;
  @include media-breakpoint-up(md) {
    padding-top: 5rem;
  }

  &__content {
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    width: 780px;
    max-width: 100%;
    margin: 0 auto;
    color: $bg-pri;
    transform: translateY(-50%);
    display: flex;
    flex-direction: column;
    z-index: 10;
    @include media-breakpoint-between(md, lg) {
      padding: 0 1.5rem;
    }
    @include media-breakpoint-between(xl, xxl) {
      width: 880px;
    }
    @include media-breakpoint-up(xxlg) {
      width: 1080px;
    }

    @include media-breakpoint-down(md) {
      padding: {
        left: 1rem;
        right: 1rem;
      }
    }
  }

  &__heading {
    font-family: $gowtham-bold !important;
    text-transform: uppercase;
    border-bottom: 1px solid $bg-pri;
    width: 100%;
  }

  &__des {
    @extend .fnt-24;
    font-weight: $fnt-700;
    max-width: 780px;
    @include media-breakpoint-between(xl, xxl) {
      max-width: 880px;
    }
    @include media-breakpoint-up(xxlg) {
      max-width: 1080px;
    }
    @include media-breakpoint-down(md) {
      font-size: 14px !important;
      line-height: 24px !important;
    }
  }

  .zw-btn {
    @include media-breakpoint-up(md) {
      margin-bottom: 3.2rem !important;
    }
  }
}

// Banner video

.zw-video-slider-banner {
  position: relative;
  height: 100%;
  width: 100%;
}

.zw-video {
  height: auto;
  width: 100%;
  object-fit: cover;
  display: block;
  @include media-breakpoint-up(md) {
    height: calc(100vh - 100px);
  }
}
.zw-mousescroll-wrap {
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
}

.zw-arrow-icon--rotate180 {
  transform: rotate(-90deg);
}

.zw-videomute-wrap {
  position: absolute;
  bottom: 2rem;
  right: 1rem;
}

.zw-video-wraper {
  position: relative;
  .zw-play-btn {
    display: none;
  }
  &.hover {
    transition: $transition;
    .zw-play-btn {
      transition: $transition;
      display: flex;
    }
  }
}

// Our numbers

.zw-ournumbers {
  text-align: left;
  display: flex;
  @include media-breakpoint-down(md) {
    flex-wrap: wrap;
  }
  @include media-breakpoint-up(md) {
    justify-content: space-evenly;
  }
  &__item {
    @include media-breakpoint-down(md) {
      width: 50%;
      margin-top: 0.5rem;
    }
    @include media-breakpoint-up(md) {
      margin-right: 2rem;
      width: 25%;
    }
    @include media-breakpoint-up(xxlg) {
      margin-right: 4rem;
    }
    &:last-child {
      margin-right: 0;
    }
  }
  &__title {
    font-size: rem(14px);
    line-height: rem(24px);
    font-weight: $fnt-700;
    display: block;
    @include media-breakpoint-up(md) {
      font-size: rem(16px);
      line-height: rem(26px);
    }
    @include media-breakpoint-up(xl) {
      font-size: rem(18px);
      line-height: rem(30px);
    }
    @include media-breakpoint-up(xxlg) {
      font-size: rem(20px);
      line-height: rem(30px);
    }
  }
  &__des {
    // font-family: $gowtham-med;
    font-weight: $fnt-500;
    font-size: rem(10px);
    line-height: rem(20px);
    @include media-breakpoint-up(md) {
      font-size: rem(12px);
      line-height: rem(22px);
    }
    @include media-breakpoint-up(xl) {
      font-size: rem(14px);
      line-height: rem(24px);
    }
  }
  &__percnt {
    font-size: rem(24px);
    line-height: rem(34px);
    font-family: $gowtham-black;
    @include media-breakpoint-up(md) {
      font-size: rem(34px);
      line-height: rem(44px);
    }
    @include media-breakpoint-up(xl) {
      font-size: rem(45px);
      line-height: rem(55px);
    }
    @include media-breakpoint-up(xxlg) {
      font-size: rem(60px);
      line-height: rem(70px);
    }
  }
}

.bannerImg {
  height: 450px;
  max-height: 100%;

  @include media-breakpoint-up(md) {
    height: calc(100vh - 120px);
  }
}

.zw-hero-bg {
  position: relative;

  img {
    @include media-breakpoint-down(md) {
      height: 210px;
    }

    @include media-breakpoint-between(md, lg) {
      height: 300px;
    }

    @include media-breakpoint-up(xl) {
      height: 400px;
      width: 100%;
    }
  }
}

.zw-overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  mix-blend-mode: multiply;

  &--dang {
    background: rgba($color: $tin-red, $alpha: 0.8);
  }

  &--info {
    background: rgba($color: #0075ff, $alpha: 0.9);
  }

  &--lightBlue {
    background: rgba($color: #0047ff, $alpha: 0.8);
  }
  &--black {
    background: rgba($color: #000000, $alpha: 0.5);
  }
}

.imgx700 {
  height: 230px;
  max-height: 100%;
  width: 100%;

  @include media-breakpoint-up(md) {
    height: 600px;
  }

  @include media-breakpoint-up(lg) {
    height: 750px;
  }
}

// Block panels

.zw-block-container {
  padding: {
    top: rem(40px);
    bottom: rem(50px);
  }

  @include media-breakpoint-up(md) {
    padding: {
      top: rem(60px);
      bottom: rem(60px);
    }
  }

  @include media-breakpoint-up(lg) {
    padding: {
      top: rem(100px);
      bottom: rem(100px);
    }
  }

  &--sec {
    padding-bottom: rem(40px);

    @include media-breakpoint-up(md) {
      padding-bottom: rem(80px);
    }

    @include media-breakpoint-up(lg) {
      padding-bottom: rem(180px);
    }
  }
}

.zw-expertise-layer {
  @include media-breakpoint-up(md) {
    position: absolute;
    top: 0px;
    height: 100%;
    overflow: hidden;
  }

  &--w45 {
    @include media-breakpoint-up(md) {
      width: 45%;
      left: 55%;
    }
  }

  &--w60 {
    @include media-breakpoint-up(md) {
      width: 50%;
      left: 50%;
    }
  }

  &__fg-img-wr {
    position: relative;

    @include media-breakpoint-up(md) {
      position: absolute;
      left: 0px;
      top: 0px;
      width: 100%;
      height: 100%;
    }
  }
}
