.img-fit {
  max-width: 100%;
  object-fit: cover;
}

.img-h-fit {
  height: 100%;
  object-fit: cover;
}

.w130 {
  width: 130px;
  height: 130px;
  object-fit: contain;
}

.bannerImg {
  @include media-breakpoint-down(md) {
    height: 430px;
  }

  @include media-breakpoint-up(md) {
    max-height: 650px;
    width: 100%;
  }
}

.img-small-icon {
  width: 28px;
  height: 28px;
}

.imgxh140 {
  @include media-breakpoint-down(md) {
    height: 140px;
    width: 100%;
  }
}

.imgh450 {
  width: 100%;
  height: 400px;
  @include media-breakpoint-up(md) {
    height: 340px;
  }
  @include media-breakpoint-up(xl) {
    height: 450px;
  }
}

.h100 {
  height: 100% !important;
}

.imgxh200 {
  width: 100%;
  height: 100%;
  object-fit: cover;
  @include media-breakpoint-down(md) {
    height: 200px !important;
  }
  &--lg {
    @include media-breakpoint-up(md) {
      height: 100% !important;
    }
  }
}
.imgxh246 {
  width: 100%;
  // height: 100%;
  object-fit: cover;
  height: 346px;
  @include media-breakpoint-up(md) {
    height: 380px;
  }
  @include media-breakpoint-up(xl) {
    height: 560px;
  }
  @include media-breakpoint-up(xxl) {
    // height: 560px;
  }
}
.imgxh100 {
  width: 100%;
  // height: 100%;
  object-fit: cover;
  height: 346px;
  @include media-breakpoint-up(md) {
    height: 100%;
    width: 100%;
    object-fit: contain;
  }
}
