// Bootstrap nav tabs pills
.zw-pills-container {
  background: $bg-pri;
  box-shadow: $boxshadow;

  &-vr {
    box-shadow: none;
  }
}

.zw-pills-list {
  display: flex;
  justify-content: space-between;

  &__item {
    width: 33.333%;
  }
}

.zw-pills-link {
  text-decoration: none;
  width: 100%;
  border-radius: 0 !important;
  display: block !important;
  margin: 0 !important;
  color: $primary !important;
  cursor: pointer;
  @extend .fnt-24;
  @include media-breakpoint-down(md) {
    font-size: rem(12px);
    line-height: rem(12px);
  }
  padding: {
    top: rem(15px) !important;
    bottom: rem(15px) !important;
  }
  @include media-breakpoint-up(md) {
    padding: {
      top: rem(20px) !important;
      bottom: rem(20px) !important;
    }
  }

  &.active {
    color: $bg-pri !important;
    background: #111d3b !important;
  }

  &::after {
    display: none;
  }
}

// Vertical tabs

.zw-pills-vr-list {
  .zw-pills-link {
    position: relative;
    border-bottom: 1px solid #c4c4c4;
    display: flex !important;
    align-items: center;
    max-height: 100%;
    color: #636363 !important;
    height: 80px;
    @include media-breakpoint-up(md) {
      height: 120px;
    }
    @include media-breakpoint-up(lg) {
      height: 155px;
    }
    @include media-breakpoint-up(xxlg) {
      height: 180px;
    }
    padding: {
      top: 0 !important;
      bottom: 0 !important;
      left: 5rem !important;
      right: 5rem !important;
    }
    @include media-breakpoint-up(md) {
      padding: {
        left: 3rem !important;
        right: 3rem !important;
      }
    }
    @include media-breakpoint-up(xl) {
      padding: {
        left: 5rem !important;
        right: 5rem !important;
      }
    }
    &:last-child {
      border-bottom: 0;
    }

    &.active {
      background: $blue !important;
      color: $bg-pri !important;
      .zw-cap {
        opacity: 1;
      }
    }
  }
}
.zw-cap {
  position: absolute;
  top: 50%;
  width: 0;
  height: 0;
  border-style: solid;
  right: 0;
  border-color: transparent transparent transparent #c4c4c4;
  border-width: 0.65em 0 0.65em 1em;
  transform: translateY(-50%) rotate(180deg);
  opacity: 0;
}

.v-pills-tabContent {
  height: 100%;
  // background: red;

  & > .active {
    display: flex !important;
    height: 100% !important;
  }
}

// Mobile tabSliders

.zw-mobile-tabslider {
  @include media-breakpoint-down(md) {
    .swiper-slide {
      display: flex;
      flex-direction: column;
      border-right: 1px solid $second;
      padding-right: 0.8rem !important;
      h2 {
        flex: 1 1 0;
      }
    }
  }
}
