.zw-hero-fig {
  height: 210px;

  @include media-breakpoint-up(lg) {
    height: 380px;
    max-height: 100%;
    width: 100%;
  }

  @include media-breakpoint-up(xxlg) {
    height: 420px;
  }

  &--sm {
    @include media-breakpoint-up(lg) {
      height: 320px;
    }

    @include media-breakpoint-up(xxlg) {
      height: 380px;
    }
  }
}

.zw-rowpanel-boxes {
  position: relative;
  margin-top: -11rem;

  @include media-breakpoint-up(lg) {
    margin-top: -19.5rem;
  }

  &__cnt {
    padding: 1.2rem 1rem 1rem 1rem;

    // padding: 2rem 2rem 1rem 2.5rem;
    @include media-breakpoint-up(md) {
      padding: 2rem 2rem 1rem 1.5rem;
    }

    @include media-breakpoint-up(lg) {
      padding: 3.8rem 5rem 2rem 6rem;
    }
  }

  .zw-unorder-list-five {
    padding-left: 1rem !important;
    padding-top: 0.5rem !important;
  }
}

.zw-unorder-list-five {
  @include media-breakpoint-up(md) {
    display: flex;
    flex-wrap: wrap;
  }

  li {
    @include media-breakpoint-up(md) {
      margin-bottom: 1rem;
      width: 33.33%;
    }

    @include media-breakpoint-up(lg) {
      width: 25%;
    }

    @include media-breakpoint-up(xxlg) {
      width: 20%;
    }
  }

  .zm-nav-link {
    // font-weight: $fnt-700;
    color: rgba($color: $ivory, $alpha: $alph9);

    @include media-breakpoint-up(lg) {
      max-width: 180px;
    }

    @include media-breakpoint-up(xxlg) {
      max-width: 210px;
    }
  }
}

// Hero panel box

.zw-herobox-cnt {
  padding: {
    top: 5rem;
    left: 6rem;
    right: 7rem;
    bottom: 3rem;
  }

  @include media-breakpoint-down(md) {
    padding: 24px;
    display: block;
  }

  display: flex;
}

.zw-thumbnail-fig {
  position: relative;

  @include media-breakpoint-down(md) {
    img {
      height: 390px;
      max-height: 100%;
    }
  }

  &__cnt {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 8%;
    right: 8%;
    color: $bg-thrice;

    @include media-breakpoint-up(md) {
      bottom: 0;
      left: 15%;
      right: 15%;
    }
  }

  &__heading {
    font-family: $gowtham-black;
    font-size: rem(24px);
    line-height: rem(34px);
    display: block;
    color: $bg-pri;

    @include media-breakpoint-up(lg) {
      font-size: rem(32px);
      line-height: rem(40px);
    }

    @include media-breakpoint-up(xxlg) {
      font-size: rem(48px);
      line-height: rem(58px);
    }
  }
}

// Water and irregation

.zw-operations-list {
  padding: 2rem !important;

  @include media-breakpoint-up(lg) {
    display: flex;
    justify-content: space-between;
  }

  &__item {
    color: $txt-clr;
    position: relative;

    @include media-breakpoint-down(md) {
      padding-bottom: 1rem !important;
      margin-bottom: 1rem !important;

      &:last-child {
        margin-bottom: 0 !important;
        padding-bottom: 0 !important;
      }
    }

    @include media-breakpoint-up(lg) {
      padding-right: 1.4rem !important;
      margin-right: 1.4rem !important;
    }

    &::before {
      content: "";
      position: absolute;
      background: $bg-thrice;
      width: 100%;
      height: 1px;
      bottom: 0;
      right: 0;

      @include media-breakpoint-up(lg) {
        top: 50%;
        width: 1px;
        height: calc(100% - 20%);
        transform: translateY(-50%);
      }
    }

    &:last-child {
      &::before {
        display: none;
      }

      margin-right: 0 !important;
      padding-right: 0 !important;
    }
  }

  &__title {
    color: $bg-pri;
    display: block;
    font-family: $gowtham-black;
    text-transform: uppercase;
  }
}

// Water and irrigation

.zw-hero-cnt-box {
  margin-top: -9rem;
  z-index: 100;
  position: relative;

  @include media-breakpoint-down(md) {
    padding-bottom: 3rem;
  }

  @include media-breakpoint-up(lg) {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
  }
}

.zw-blocklist-vr {
  &__item {
    color: $bg-pri;
    margin-top: 1rem;

    @include media-breakpoint-up(md) {
      margin-top: 1.5rem;
    }
  }

  &__title {
    display: block;
    @extend .fnt-gow;

    @include media-breakpoint-up(md) {
      text-transform: uppercase;
    }
  }
}

.zw-btm-line {
  position: relative;
  padding-bottom: 1rem;

  &::before {
    content: "";
    position: absolute;
    height: 1px;
    width: 97px;
    max-width: 100%;
    background: #e4ded3;
    bottom: -1rem;
  }
}

// Injection modeling

.zw-bn-longtxt {
  @include media-breakpoint-down(md) {
    h2 {
      height: 100%;
      bottom: 35% !important;
    }
  }
}


