// finishing touch

.zw-sand-casting-finishing-touch {
    // padding      : 32px 0 20px 0;
    position     : relative;
    // background: #E4DED3;

    @include media-breakpoint-up(md) {
        // padding: 64px 0 80px 0;
    }

    .zw-sand-casting-finish-card {
        position: relative;

        .card-heading {
            position            : absolute;
            top                 : 50%;
            /* left             : 50%; */
            /* -webkit-transform: translate(-50%,-50%); */
            // -ms-transform: translate(-50%,-50%);
            /* transform        : translate(-50%,-50%); */
            transform           : translateY(-50%);
            left                : 24px;
            right               : 24px;

            @include media-breakpoint-up(md) {
                left     : 60px;
                max-width: 220px;
            }

            @include media-breakpoint-up(lg) {
                left     : 100px;
                max-width: 330px;
            }

            @include media-breakpoint-up(xxlg) {
                left     : 150px;
                max-width: 430px;

            }

            h2 {
                color: #FAF3E7 !important;

                @include media-breakpoint-up(md) {
                    margin-right: 1rem;
                }

                // padding-right: 5rem;
            }

            p {
                color           : #E4DED3 !important;
                // padding-right: 5rem;
            }
        }

        figure {
            img {
                width : 100%;
                height: 400px;

                @include media-breakpoint-up(md) {

                    height: 600px;
                }

                @include media-breakpoint-up(xxlg) {
                    height: 800px;
                }
            }

        }
    }



}