.zw-btn {
  @extend .fnt-16;
  font-family: $gowtham-bold;
  padding: rem(10px) rem(18px);
  text-align: center;
  display: inline-flex;
  align-items: center;
  text-decoration: none;
  transition: $transition;

  @include media-breakpoint-up(md) {
    padding: rem(12px) rem(25px);
  }

  @include media-breakpoint-up(lg) {
    padding: rem(14px) rem(27px);
  }

  &--default {
    border: 1px solid $bg-pri;
    background: $bg-pri;
    color: rgba($color: $primary, $alpha: 0.9);
  }

  &--pri {
    background: $bg-sec;
    color: $white;
    border: 1px solid $bg-sec;

    &:hover,
    &:focus {
      color: $bg-sec;
      transition: $transition;
      background: transparent;
    }
  }

  &--secondary {
    background: $thrice;
    color: $white;

    &:hover {
      color: $white;
    }
  }

  // small
  &--sm {
    max-width: 140px;
    width: 100%;
  }

  &--xs {
    padding-left: 1.5rem !important;
    padding-right: 1.5rem !important;
  }
}

// Play buttons

.em-play-btn {
  height: rem(40px);
  width: rem(40px);
  @include border-radius(50%);
  border: 2px solid $white;

  @include media-breakpoint-up(lg) {
    border: 4px solid $white;
    height: rem(70px);
    width: rem(70px);
  }

  &--abs {
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    left: 50%;
    z-index: 10;
    cursor: pointer;

    &:hover {
      background: #60c6f7;
      transition: background 0.4s ease-in;
      border: 3px solid $white;
    }
  }
}

.play-icon {
  height: rem(15px);
  position: relative;
  left: 2px;

  @include media-breakpoint-up(lg) {
    height: rem(28px);
  }
}

// Slider icons

.icon-arrow {
  font-size: 20px;

  &--white {
    color: $white;
  }
}

// BUTTON FOCUS

.btn-check:focus + .btn,
.btn:focus {
  outline: 0;
  box-shadow: none;
  border: 1px solid #e2e2ff;
}
