.zw-list-block {
  display: flex;

  &__item {
    width: 100%;

    @include media-breakpoint-up(md) {
      width: 33.33%;
      min-height: 218px;
    }

    &:first-child .zw-list-block__item-iner-text {
      border-left: 0;
    }

    .zw-list-block__item-iner-text {
      position: relative;
      animation: fade-in 1s;
      cursor: pointer;

      @include media-breakpoint-up(md) {
        &::before,
        &::after {
          content: "";
          position: absolute;
          height: 2px;
          width: 84%;
          opacity: 0;
          left: 0;
        }

        &::before {
          top: -1.4rem;
        }

        &::after {
          bottom: -1.4rem;
        }
      }

      &:hover {
        @include media-breakpoint-up(md) {
          &::before,
          &::after {
            animation: fade-in 1s;
            opacity: 1;
          }
        }
      }
    }
  }

  &__item-iner-text {
    position: relative;
    border-left: 1px solid $black;

    &.Light {
      border-left: 1px solid $second;

      @media (max-width: 1199px) {
        border-left: 1px solid transparent;
      }
    }
  }

  .collapse:not(.show) {
    @include media-breakpoint-down(md) {
      display: block !important;
    }
  }

  // White
  &--white {
    color: $txt-clr;

    .zw-list-block__item {
      width: 50%;
    }

    .zw-listitem-heading {
      color: #e4ded3;
    }

    .zw-list-block__item-iner-text {
      border-color: #e4ded3;

      padding : {
        left: 1.5rem;
        right: 1.5rem;
      }

      &::before,
      &::after {
        background: $bg-pri;
      }
    }
  }

  // Default list
  &--init {
    align-items: center;

    @include media-breakpoint-down(md) {
      margin-left: -1rem;
    }

    @include media-breakpoint-between(md, lg) {
      padding-top: 3rem !important;
      margin-left: -1rem !important;
    }

    .zw-list-block__item-iner-text {
      padding-left: 1rem !important;
      height: auto;

      @include media-breakpoint-down(md) {
        padding-right: 1.5rem !important;
      }

      @include media-breakpoint-up(md) {
        padding-right: 1rem !important;
        padding-left: 1.5rem !important;
      }

      &::before,
      &::after {
        background: $black;
        left: 50%;
        transform: translateX(-50%);
      }

      .zw-btn {
        @include media-breakpoint-up(md) {
          display: none;
        }
      }

      @include media-breakpoint-up(md) {
        &:hover {
          border-left: 0;

          .zw-btn {
            display: inline-flex;
            animation: fade-in 1s;
          }
        }
      }
    }
  }
}

.zw-listitem-heading {
  font-family: $gowtham-bold !important;
}

// Order-list
.ZW-unOrderList {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  column-count: 1;
  @extend .mb-0;

  @include media-breakpoint-down(md) {
    justify-content: space-between;
  }

  @include media-breakpoint-up(lg) {
    column-count: 2;
  }

  li {
    width: 100%;
    @extend .mb-2;
    @extend .mb-lg-5;
    // width: 46%;
    @include media-breakpoint-down(md) {
      p {
        margin-bottom: 0.2rem !important;
      }
    }

    @include media-breakpoint-up(md) {
      width: 50%;
    }
  }

  li:last-child {
    @extend .mb-0;
  }
}

.zw-unorder-list,
.ZW-unOrderList {
  padding-left: 1.5rem !important;
  padding-top: 1.2rem !important;

  .zm-nav-link,
  &__desc {
    @extend .fnt-21;
    margin-bottom: 0.8rem;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: -0.8rem;
      height: 5px;
      width: 5px;
      border-radius: 50%;
      background: $txt-clr;
    }
  }

  &__desc {
    @extend .fnt-24;
    @extend .txt-clr;
    @extend .fnt-500;

    @include media-breakpoint-up(lg) {
      margin-right: 30px;
    }

    &::before {
      top: 15px;
    }
  }

  &--sec {
    display: flex;
    flex-wrap: wrap;
    li {
      width: 50%;
      text-transform: capitalize;
    }
    .zm-nav-link {
      font-family: $Bitter;
      font-size: 18px !important;
      line-height: 28px !important;
      margin-top: 2rem;
      margin-bottom: 2rem;
    }
  }
  &--cnt {
    font-family: $Bitter;
    padding-right: 3rem !important;
    color: $txt-clr;
    li {
      position: relative;
      margin-bottom: 1.2rem;
      @include media-breakpoint-up(md) {
        margin-bottom: 2.5rem;
      }
      &:last-child {
        margin-bottom: 0;
      }
      &::before {
        content: "";
        position: absolute;
        top: 12px;
        left: -0.8rem;
        height: 5px;
        width: 5px;
        border-radius: 50%;
        background: $txt-clr;
      }
    }
    p {
      text-transform: capitalize;
    }
  }
  &__title {
    @extend .fnt-21;
    display: block;
    text-transform: capitalize;
    font-weight: $fnt-700;
  }
}

// list info

.zw-unorder-list-four {
  padding-right: 0 !important;
  @include media-breakpoint-up(md) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    li {
      width: 100%;
      max-width: 280px;
      padding-right: 2rem !important;
      margin-bottom: 4.5rem !important;
    }
  }
}

.ZW-unOrderList--secondary {
  column-count: 2;

  @include media-breakpoint-up(lg) {
    column-count: 2;
  }

  li {
    width: 50%;
    @extend .mb-2;
    @extend .mb-xl-5;

    @include media-breakpoint-up(md) {
      width: 45%;
    }
  }

  p {
    @extend .second09;

    &::before {
      background: $second09;
    }
  }
}

.ZW-unOrderList--primary {
  column-count: 2;

  @include media-breakpoint-up(lg) {
    column-count: 4;
  }

  li {
    width: 45%;
    @extend .mb-2;
    @extend .mb-xl-5;

    @include media-breakpoint-up(md) {
      width: 33%;
    }

    @include media-breakpoint-up(xl) {
      width: 24%;
    }
  }

  p {
    @extend .second09;

    &::before {
      background: $second09;
    }
  }
}

// .zw-second-unorderList
.zw-second-unorderList {
  @extend .my-3;
  @extend .my-lg-4;
  display: flex;

  .Item {
    display: flex;
    flex-direction: column;
    margin-right: 20px;
    padding-right: 20px !important;
    border-right: 1px solid $bg-thrice;
    @include media-breakpoint-up(md) {
      margin-right: 30px;
      padding-right: 30px !important;
    }
    &:last-child {
      border: none;
      margin-right: 0;
      padding-right: 0 !important;
    }
  }

  .desc {
    @extend .fnt-21;
    @extend .txt-clr;
  }

  .h4Title {
    color: $bg-pri;
    font-weight: 900;
    font-family: $gowtham-bold;
    @include media-breakpoint-down(md) {
      font-size: 18px !important;
      line-height: 30px !important;
    }
  }
}
