@charset "UTF-8";

@font-face {
  font-family: "zetworks";
  src: url("../fonts/zetworks/zetworks.eot");
  src: url("../../fonts/zetworks/?#iefix") format("embedded-opentype"),
    url("../fonts/zetworks/zetworks.woff") format("woff"),
    url("../fonts/zetworks/zetworks.ttf") format("truetype"),
    url("../fonts/zetworks/zetworks.svg#zetworks") format("svg");
  font-weight: normal;
  font-style: normal;
}

[data-icon]:before {
  font-family: "zetworks" !important;
  content: attr(data-icon);
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "zetworks" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-twitter:before {
  content: "\61";
}
.icon-insta:before {
  content: "\62";
}
.icon-youtube:before {
  content: "\63";
}
.icon-arrow:before {
  content: "\65";
}
.icon-facebook:before {
  content: "\64";
}

.icon {
  display: inline-flex;
}
