.zw-block-column {
  padding: rem(40px) rem(30px);
  position: relative;

  @include media-breakpoint-up(lg) {
    padding: rem(60px);
  }

  @include media-breakpoint-up(xl) {
    padding: rem(80px);
  }

  &__item {
    display: flex;
    flex-direction: column;
    z-index: 1;
    position: relative;

    @include media-breakpoint-up(md) {
    }

    @include media-breakpoint-up(lg) {
      align-items: flex-start;
      height: 100%;
    }
  }

  &--right {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .imgx700 {
    @include media-breakpoint-down(md) {
      height: 320px;
    }
  }
}

.zw-expertise-layer__fg-img-wr {
  &.-layer {
    position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
  }
}

#contentList {
  .notRequired {
    display: none;
  }
}

.zw-rect-box .boxlink {
  display: none;
  &.active {
    display: block;
  }
}

.NetwerkLists {
  .active h2 {
    color: $tick-blue !important;
  }
}

// #NetwerkBoxlink0 {
//   display: block;
// }

g.active {
  .zw-bg-change {
    fill: #192954 !important;
  }
}

h2.inactive {
  color: $black;
}

h2.active {
  color: $tick-blue;
}

.NetwerkLists {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;

  li {
    @media (min-width: 1356px) {
      position: absolute;
    }
  }
}

.NetwerkLink {
  cursor: pointer;
}

.NetwerkListPane {
  position: relative;
}

.NetwerkLists {
  @media (min-width: 1356px) {
    &__item1 {
      left: 5%;
      top: 5%;
    }

    &__item2 {
      right: 5%;
      top: 5%;
    }

    &__item3 {
      right: 5%;
      bottom: 5%;
    }

    &__item4 {
      left: 5%;
      bottom: 5%;
    }

    &__item5 {
      bottom: -5%;
      left: 40%;
    }
  }
}

.zw-global-svg {
  cursor: pointer;
}
