.zw-footer {
  background: #000b36;
  padding-top: 2rem;
  padding-bottom: 2rem;
  color: $white;

  @include media-breakpoint-up(md) {
    padding-top: rem(55px);
    padding-bottom: rem(45px);
  }

  &__left-wrap {
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    @include media-breakpoint-down(lg) {
      img {
        max-width: 180px;
      }
    }
    @include media-breakpoint-up(lg) {
      padding-right: 1rem;
    }
  }

  &__des {
    max-width: rem(510px);
  }

  &__nav-heading {
    font-size: rem(18px);
    line-height: rem(24px);
    font-family: $gowtham-bold;
    color: $bg-pri;
    margin-bottom: 1rem;
  }
}

.zw-footer-blocklist {
  padding-top: rem(35px);

  @include media-breakpoint-up(lg) {
    display: flex;
    justify-content: space-between;
  }

  &__item {
    margin-right: 2rem;

    @include media-breakpoint-down(lg) {
      border-bottom: 1px solid #e4ded3;
      margin-bottom: 1.5rem;

      &:last-child {
        border-bottom: none;
        margin-bottom: 0;
      }
    }

    &:last-child {
      margin-right: 0;
      // @include media-breakpoint-up(lg) {
      //   width: 28%;
      // }
    }
  }
}

// Socail list
.zw-social-link {
  color: $white;
  text-decoration: none;
  margin-right: 0.5rem;
  height: rem(30px);
  width: rem(30px);
  background: rgba($color: $bg-pri, $alpha: 0.1);
  border-radius: 50%;
  transition: $transition;

  @include media-breakpoint-up(xxlg) {
    margin-right: 1rem;
  }

  &:last-child {
    margin-right: 0;
  }

  &:hover {
    background: $white;
    color: $black;
    transition: $transition;
  }

  &:hover .linkfill path {
    fill: $black;
  }
  &--gray {
    background: $black01;
    color: $black;
  }
}

.zw-icon {
  font-size: 22px;
}

// Footer navlist

.zm-nav-link {
  position: relative;
  font-size: rem(16px);
  line-height: rem(26px);
  color: #e4ded3;
  text-decoration: none;
  margin-bottom: 1.2rem;
  display: inline-block;
  transition: $transition;
  cursor: pointer;

  @media (min-width: 992px) and (max-width: 1550px) {
    font-size: rem(14px) !important;
    line-height: rem(24px) !important;
  }

  &::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -0.2rem;
    width: 100%;
    height: 1px;
    background-color: $white;
    transition: $transition;
    transform-origin: left;
    transform: scaleX(0);
  }

  &:hover,
  &:focus {
    color: $white;
    transition: $transition;

    &::after {
      transform: scaleX(1);
    }
  }
}

.zm-copyrgt {
  font-family: "GothamLight";
  color: $bg-pri;
  padding-top: rem(40px);

  @include media-breakpoint-down(md) {
    width: 270px;
    margin: 0 auto;
  }

  @include media-breakpoint-up(md) {
    padding-top: rem(70px);
  }
}
